@use "reset.css";
@use "_helpers.scss";
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');
:root {
  --bg-color: #111013;
}
html,
body {
  height: 100%;
  background-color: var(--bg-color);

  box-sizing: border-box;
  font-family: "Roboto Mono", monospace;
  color: #fff;
}

.section {
  height: 100%;
  box-sizing: border-box;
}
.home {
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(217, 217, 217, 0) 11.98%,
    rgba(217, 217, 217, 0.15) 97.92%
  );
  .grid_fondant{
    display: grid;
  
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    .fondant{
      border-bottom-left-radius:50px; 
      border-bottom-right-radius:100px;
      // background:rgba(216, 19, 19, 0.664)
    }
  }
  .content {
    @extend %centering;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;
    position: relative;
    #butterfly {
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      fill: #957ec4c0;
      .left_wing,
      .right_wing,
      .body {
        transform-origin: center;
      }
    }
    .logo_container {
      width: 90%;
      max-width: 400px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 35px;
    //   transform: translateX(-10px);
    }
    .bakery_name_container {
      width: 80%;
      max-width: 300px;
      transform: translateX(20px);
    }
    .choix_parfum {
      @extend %centering;
      flex-direction: column;
      gap: 15px;
      .choix_container {
        @extend %centering;
        gap: 15px;
        .parfum {
          cursor: pointer;
          width: 40px;
          transition: scale 1s ease;
          
          &:hover {
              transform: scale(1.1) !important;
            }
            > img {
              
            width: 100%;
            object-fit: contain;
          }
        }
      }
    }
  }
}

.presentation{
  font-family: 'Ubuntu', sans-serif;
  .content{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap-reverse;
    margin: 50px;
    .text_container {
      font-size: 1.5rem;
      align-self: center;
      .text_line{
        position: relative;
      }
      .line_mask {
        z-index: 5;
        background-color: #111013;
        position: absolute;
        opacity: 0.5;
        width: 100%;
        height: 100%;
        top: 0;
        /* left: 0; */
        right: 0;
    }
  }
    .image_container{
      position: relative;

      .background_image{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: translate(-123px, -37px) scale(1.1);
        z-index: -1;
        /* max-width: 500px; */
        /* max-height: 600px; */
      }
      .image{
        width: auto;
        height: 75vh;
        > img{
          max-width: 100%;
          max-height: 100%;
          object-fit: contain;
          border-radius: 5px;
        }
      }
    }
  }
}